@import "../variables.scss";

.side__search__container__search {
  color: rgb(83, 100, 113);
  width: 100%;
  max-width: 100%;
  height: 40px;
  cursor: default;
  pointer-events: auto;
  .side__search__container__search__form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    .side__search__container__search__form__div {
      width: 100%;
      border-radius: 25px;
      border: 1px solid rgba(0, 0, 0, 0);
      background-color: #eff3f4;
      transition: all;
      display: flex;
    }
    .side__search__container__search__form__div:focus-within {
      border: 1px solid $color-main;
      background-color: rgb(255, 255, 255);
      button {
        color: $color-main;
      }
    }
    label {
      display: flex;
      flex-direction: row;
      width: 100%;
      .side__search__container__search__form__btn {
        padding-left: 12px;
        min-width: 32px;
        flex-shrink: 0;
      }
      .side__search__container__search__form__input {
        padding: 12px;
        cursor: text;
        flex-grow: 1;
      }
    }
  }
  .side__search__container__search__modal {
    position: relative;
    width: inherit;
  }
}

.side__search__container {
  display: none;
  z-index: 2;
  position: fixed;
  top: 0;
  background-color: rgba(255, 255, 255, 1);
  margin-bottom: 12px;
  width: 350px;
  min-height: 32px;
  height: 53px;
  align-items: center;
  flex-direction: column;
  pointer-events: auto;
  flex-grow: 1;
  justify-content: center;
}
.side__search__container.show {
  display: flex;
  pointer-events: visible;
}

//
.side__search-box {
  display: none;
  height: 53px;
}
.side__search-box.show {
  display: block;
  pointer-events: visible;
}
